<template>
  <div class="filelook">
    <div class="content">
      {{ filetext }}6546555555555555555555555555555555555555555555
    </div>
    <div class="close" @click="openfilelook(2)">结束预览</div>
  </div>
</template>

<script>
export default {
  props: ["openfilelook", "file"],
  mounted() {
    this.readfile();
  },
  data() {
    return {
      filetext: "",
    };
  },
  methods: {
    readfile() {
      //保留全局this
      var that = this;
      var reader = new FileReader();
      reader.readAsText(this.file);
      reader.onload = function () {
        that.filetext = this.result;
      };
    },
  },
};
</script>

<style lang="less" scoped>
.filelook {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 100;
}
.content {
  width: 100%;
  height: 50vh;

  color: #fff;
  //   background-color: #fff;
  font-size: 1.4rem;
}
.close {
  position: absolute;
  bottom: 2px;
  font-size: 1.5rem;
  height: 3rem;
  line-height: 3rem;
  width: 30%;
  margin-top: 3rem;
  margin-left: 40%;
  text-align: center;
  border-radius: 1rem;
  color: #fff;
  background-color: rgb(238, 43, 43);
}
</style>