<script>
import imglist from "../components/imglist";
import filelook from "../components/filelook";

export default {
  components: {
    imglist,
    filelook,
  },
  mounted() {
    this.scrollbottom();
  },

  data() {
    return {
      chatlist: [], //聊天列表
      inputcontent: "", //聊天框中的内容
      istool: false, //开关工具栏
      isinput: false, //判断是否正在输入
      ischooseimg: false, //控制imglist组件的开关
      isfile: false, //控制file组件的开关
      islookfile: false, //控制file预览组件的开关

      chatitem: {
        //临时单条信息记录
        content: null, //输入框中的文本信息
        img: null, //图片信息
        file: null, //文件信息
        //头像默认
        headerimg:
          "https://dss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3155998395,3600507640&fm=26&gp=0.jpg",
      },
    };
  },

  methods: {
    //让滚动条保持在底部
    scrollbottom() {
      let main = this.$refs.main;
      main.scrollTop = main.scrollHeight;
    },

    //控制图片上传组件的开关
    openimgpage(key) {
      if (key == 1) {
        this.ischooseimg = true;
      }
      if (key == 2) {
        this.ischooseimg = false;
      }
    },

    //开关工具栏按钮
    //当工具栏打开，main部分高度变低40vh
    opentoolbox() {
      this.istool = !this.istool;
      this.scrollbottom();
    },
    //input开关工具栏
    opentoolbox2(key) {
      if (key == 1) {
        this.istool = true;
        this.isinput = true;
        this.scrollbottom();
        this.openimgpage(2);
      }
      if (key == 2) {
        this.istool = false;
        this.isinput = false;
        this.scrollbottom();
        this.openimgpage(2);
      }
    },
    //文件预览开关
    openfilelook(key) {
      if (key == 1) {
        this.islookfile = true;
        this.scrollbottom();
      }
      if (key == 2) {
        this.islookfile = false;
        this.scrollbottom();
      }
    },

    //发送文本框消息
    sendmsg() {
      if (this.inputcontent == "") {
        alert("不允许空行发送");
      } else {
        this.chatitem.content = this.inputcontent;
        this.inputcontent = "";
        var a = JSON.parse(JSON.stringify(this.chatitem));
        //每次赋值完将临时的chatitem获取的值清空
        this.chatitem.content = null;
        this.chatlist.push(a);
        this.scrollbottom();
      }
    },

    //从图片列表组件获取图片并发送
    getimg(imgurl) {
      console.log(imgurl);
      this.chatitem.img = imgurl;
      var a = JSON.parse(JSON.stringify(this.chatitem));
      //每次赋值完将临时的chatitem获取的值清空
      this.chatitem.img = null;
      this.chatlist.push(a);
      this.scrollbottom();
    },

    clickLoad() {
      this.$refs.refFile.dispatchEvent(new MouseEvent("click"));
    },
    fileLoad() {
      const selectedFile = this.$refs.refFile.files[0];
      // console.log(selectedFile);

      this.chatitem.file = selectedFile;

      var a = { ...this.chatitem };
      //每次赋值完将临时的chatitem获取的值清空
      this.chatitem.file = null;
      // console.log(a);
      this.chatlist.push(a);

      console.log(this.chatlist);
      // var reader = new FileReader();
      // reader.readAsText(selectedFile);
      // reader.onload = function () {
      //   console.log(this.result);
      // };
    },
  },
};
</script>
